import {ADD_REMINDER, CLEAR_REMINDERS, DELETE_REMINDER, SERVER_URL} from '../constants';
import axios from 'axios'
import moment from "moment";

export const addReminder = (id, text, dueDate=0, timestamp, category, checked, eID) => {
    console.log('action in addReminder: ', id, text, dueDate, timestamp, category, checked, eID);
    return {
        id,
        type: ADD_REMINDER,
        text,
        dueDate,
        timestamp,
        category,
        checked,
        eID
    };
}

export const deleteReminder = (id) => {
    return {
        type: DELETE_REMINDER,
        id
    }
}

export const deleteTask = (id, task, eventID) => {
    const url = SERVER_URL + 'task/delete/' + id + '/' + eventID
    return function(dispatch){
        return axios({
            method: 'delete',
            url,
            data: {
                task
            }
        }).then(({data}) => {
            if(data.response === 'success' && data.code === 200){
                dispatch(deleteReminder(id))
            }
        })
    }
}

export const emptyDB = () => {
    const url = SERVER_URL + 'tasks/delete'
    return function(dispatch){
        return axios.post(url).then(({data}) => {
            if(data.code !== 200){
                console.error('Error: ', data.response, ' ', data.body)
            }
            else{
                console.log('axios response: ', data)
                dispatch(clearReminders())
            }
        })
    }
}

export const fetchTasks = () => {
    const url = SERVER_URL + 'tasks/all'
    return function(dispatch){
        return axios.get(url).then(({data}) => {
            if(data){
                for(const record of data){
                    console.log('record: ', record)
                    const dueDate =  new Date(record.taskYear,record.taskMonth,record.taskDay,record.taskHour,record.taskMinute,record.taskSecond).toISOString();
                    const taskContent = record.task
                    const timestamp = record.timestamp
                    const category = record.category
                    const checked = record.taskChecked;
                    const eID = record.eID;
                    dispatch(addReminder(record.id, taskContent,dueDate, timestamp, category, checked, eID))
                }
            }
            else{
                console.error('Error: ', data)
            }
        })
    }
}

export const fetchLastID = () => {
    const url = SERVER_URL + 'tasks/id'
    axios.get(url).then(({data}) => {
        if(data.response === 'success' && data.code === 200 ){
            return data.body;
        }
    })
}

export const addTask = (taskName, timestamp, category) => {

    console.log('fID:' ,fetchLastID())

    const headers = {
        'Content-Type': 'application/json'
    }

    const date = moment(new Date(timestamp))
    const tID = fetchLastID() + 1;
    console.log('tID: ', tID )
    const taskDay = date.date()
    const taskMonth = date.month() + 1
    const taskYear = date.year()
    const taskHour = date.hour()
    const taskMinute = date.minute()
    const taskSecond = date.second()
    const url = SERVER_URL + 'task/add'
    return function(dispatch){
        return axios({
            method: 'post',
            url,
            headers: headers,
            data:[
                {
                    content: taskName,
                    category,
                    taskChecked: 0,
                    taskYear,
                    taskMonth,
                    taskDay,
                    taskSubs: {},
                    taskPriority: 0,
                    tid: Math.floor(Math.random()* (1000-50)),
                    taskHour,
                    taskMinute,
                    taskSecond
                }
            ]

        }).then(({data}) => {
            console.log('addTask axios response: ', data)
            if(data.code === 200 && data.response === 'success'){
                console.log('task added successfully!')
                console.log(data.body[0], data.body[1], data.body[2], data.body[3], data.body[4])
                dispatch(addReminder(data.body[0], data.body[1], data.body[2], data.body[3], data.body[4], 0))
            }
        })
    }
}



export const clearReminders = () => {
    console.log('clearReminders called')
    return {
        type: CLEAR_REMINDERS
    };
}