import {ADD_REMINDER, CLEAR_REMINDERS, DELETE_REMINDER} from '../constants';

const reminder = (action) => {
    let { id, text, dueDate, timestamp, category, checked, eID } = action;
    return {
        id,
        text,
        dueDate,
        timestamp,
        category,
        checked,
        eID
    }
}

const removeById  = (state = [], id) => {
    return state.filter(reminder => reminder.id !== id);
}

const reminders = (state = [], action) => {
    let reminders = null;
    switch(action.type) {
        case ADD_REMINDER:
            reminders = [...state, reminder(action)];
            return reminders;
        case DELETE_REMINDER:
            reminders = removeById(state, action.id);
            return reminders;
        case CLEAR_REMINDERS:
            reminders = [];
            return reminders;
        default:
            return state;
    }
}


export default reminders;