import React, {Component} from 'react'
import { connect } from 'react-redux'
import moment from 'moment'
import { Badge, Button} from 'react-bootstrap'
import { addReminder, deleteReminder, clearReminders, emptyDB, fetchTasks, addTask, deleteTask } from '../actions'

const capitalizeFirstLetter = ([ first, ...rest ], locale = navigator.language) =>
    first.toLocaleUpperCase(locale) + rest.join('')

class App extends Component {

    componentDidMount() {
        this.props.clearReminders()
        this.props.fetchTasks()
    }

    constructor(props) {
        super(props);
        this.state = {
            text: '',
            dueDate: '',
            category: '',
            tags: [],
            checked: 0
        }
    }

    addReminder() {
        this.props.addTask(this.state.text, this.state.dueDate, this.state.category);
        let inputFields = document.querySelectorAll('input');
        inputFields.forEach(input => input.value = '');
    }

    deleteReminder(id, task, eventID) {
        this.props.deleteTask(id, task, eventID);
    }

    emptyDB() {
        this.props.emptyDB()
    }

    fetchTasks() {
        this.props.fetchTasks()
    }

    renderReminders() {
        const { reminders } = this.props;
        return (
            <ul className="list-group">
                {
                    reminders.map(reminder => {
                        return (
                            <div key={Math.floor(Math.random() * 100)} className="reminder-tasks">
                                <li className="list-group-item reminder-items"  key={reminder.id}>
                                    <div className="list-item">
                                        {
                                            reminder.checked ?
                                                <div style={{textDecorationLine: 'line-through'}}>
                                                    {reminder.text}
                                                </div> : <div>{reminder.text}</div>
                                        }
                                        {/*<div><em></em></div>*/}
                                        {/*<div><em>{moment(new Date((reminder.timestamp))).format("DD/MM/YYYY")}</em></div>*/}
                                        <div>
                                            <Badge style={{backgroundColor: '#007bff', color: '#fff'}}>
                                                {moment(new Date(reminder.dueDate)).subtract(1,'M').fromNow()}
                                            </Badge>
                                            <Badge color="info" style={{backgroundColor: '#2ecc71', color: '#fff', marginLeft: '5px'}}>
                                                {capitalizeFirstLetter(reminder.category)}
                                            </Badge>
                                            <Badge style={{backgroundColor: '#dc3545', color: '#fff', marginLeft: '5px'}}>
                                                Created at: {moment(new Date((reminder.timestamp))).format("DD/MM/YYYY")}
                                            </Badge>
                                        </div>
                                    </div>
                                    <div
                                        className="list-item delete-button"
                                        onClick={() => this.deleteReminder(reminder.id, reminder.text, reminder.eID)}
                                    >
                                        &#x2715;
                                    </div>
                                </li>
                            </div>
                        )
                    })
                }
            </ul>
        )
    }

    render() {
        return(
            <div className="App">
                <div className="sandbox sandbox-correct-pronounciation">
                    <h1 className="heading heading-correct-pronounciation">
                        <var style={{textTransform: 'none'}}>Tasse de Th√©</var>
                    </h1>
                </div>
                <div className="form-inline reminder-form">
                    <div className="form-group">
                        <input
                            className="form-control"
                            placeholder="I have to..."
                            onChange={event => this.setState({text: event.target.value})}
                        />
                        <input
                            className="form-control"
                            placeholder="Category"
                            onChange={event => this.setState({category: event.target.value})}
                        />
                        <input
                            className="form-control"
                            type="datetime-local"
                            onChange={event =>
                                this.setState({
                                    dueDate: event.target.value})}
                        />
                    </div>

                    <Button
                        type="button"
                        className="btn btn-success"
                        onClick={ () =>  this.addReminder()}
                    >
                        Add a new task
                    </Button>

                    <div
                        className="btn btn-danger"
                        onClick={() => this.props.emptyDB()}
                    >
                        Clear all tasks
                    </div>
                    { this.renderReminders() }
                </div>
            </div>

        )
    }


}

function mapStateToProps(state) {
    return {
        reminders: state
    }
}
export default connect(mapStateToProps, { addReminder, deleteReminder, clearReminders, emptyDB, fetchTasks, addTask, deleteTask})(App);